import React, { useState, useEffect } from "react";
import {
  Card,
  Grid,
  Container,
  Header,
  Search,
  Segment,
  Button,
  Image,
  Transition,
  List,
} from "semantic-ui-react";
import axios from "axios";

const DeckInfo = (props) => {
  return (
    <Card.Group>
      <Card>
        <Card.Content>
          <Card.Header>User</Card.Header>
          <Card.Meta>My Custom Deck</Card.Meta>
          <Card.Description>
            This is the info about your deck<br></br>
            {props.deck.length}/60
          </Card.Description>
        </Card.Content>
        <Card.Content extra>
          <div className="ui two buttons">
            <Button color="green">Download Excel</Button>
            <Button basic color="red" onClick={props.reset}>
              Reset
            </Button>
          </div>
        </Card.Content>
      </Card>
      <Card color="black" style={{padding:"1em"}}>
        <Card.Content>
          <Card.Description>
            <Grid>
              <Grid.Row>
                <List horizontal>
                  {props.deck.map((card) => (
                    <List.Item key={card.id}>{card.name}</List.Item>
                  ))}
                </List>
              </Grid.Row>
            </Grid>
          </Card.Description>
        </Card.Content>
      </Card>
    </Card.Group>
  );
};

function Layout() {
  const [cards, setCards] = useState([]);
  const [search, setSearch] = useState("");
  const [loading, setLoading] = useState(false);
  const [deck, setDeck] = useState([]);

  useEffect(() => {
    axios
      .get("/card?name=" + search)
      .then((response) => {
        setCards(response.data || []);
        setLoading(false);
      })
      .catch((err) => {
        setCards([]);
        setLoading(false);
      });
  }, [search]);

  function fetchData() {
    axios
      .get("/card?name=pikachu")
      .then((response) => {
        console.log(response.data);
        setLoading(true);
        setCards(response.data || []);
      })
      .catch((err) => {
        setCards([]);
        console.log(err);
      });
  }

  function handleSeach(event) {
    if (event.target.value.length > 3) {
      setSearch(event.target.value);
      setLoading(true);
    } else {
      setSearch("");
    }
  }

  function sumCard(item) {
    axios
      .post("/check", { deck: deck, item_id: item.id })
      .then((response) => {
        if(response.data.check === true){
          setDeck([...deck, item]);
        } else {
          alert('no puede agregar esta carta')
        }
      })
      .catch((err) => {
      });
  }

  return (
    <Container>
      <Segment inverted stacked>
        <h2>Deck Builder</h2>
        <Header>
          <Search
            size="mini"
            open={false}
            loading={loading}
            onSearchChange={(e) => handleSeach(e)}
          ></Search>
        </Header>
        <DeckInfo deck={deck} reset={() => setDeck([])}></DeckInfo>
      </Segment>
      <Segment inverted style={{ padding: "2em" }}>
        <Container>
          <Grid>
            {cards.map((card) => (
              <Button
                onClick={() => sumCard(card)}
                style={{ width: "20%", marginBottom: "1em", padding: "0" }}
                content={<Image src={card.imageUrl}></Image>}
                key={card.id}
              ></Button>
            ))}
          </Grid>
        </Container>
      </Segment>
      <Segment inverted></Segment>
    </Container>
  );
}

export default Layout;



